@import "https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;700&display=swap";

// vars
$font: 'Roboto Mono';
$fontColor: white;
$secondaryColor: #6212ece6;
$errorColor: rgba(249, 80, 80, 0.993);
$background: #0f0f0f;
$lighterBackground: #313337e6;

////////////////////////////
/////// main styles ////////
////////////////////////////

* {
	color-scheme: dark;
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

body {
	font-family: $font;
	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 min(500px, 10vw);
	width: 100vw;
	height: 100vh;
	background-color: $background;
}

////////////////////////////
/////// class styles ////////
////////////////////////////

.typingArea {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.result {
	align-items: center;
	justify-content: center;
	flex-direction: column;
	font-size: 1.4em;
}

.words {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 0 0.7ch;

	> .word {
		padding: 4px;
		margin: 3px 0;
		border-radius: 5px;
		transition: all ease .23s;
		color: $fontColor;
	}

	> .word.active {
		background-color: $lighterBackground;
	}

	> .word.error {
		color: $errorColor;
	}

	> .word.currentlyError {
		background-color: $errorColor;
	}
}

.inputBox {
	width: min(600px, 70%);
	height: 30px;
	font-size: 1.1em;
	margin-top: 20px;
	padding: 20px 5px;
}

.wpm {
	display: flex;

	> .num {
		font-size: 2em;
	}
}

#playaudio {
	position: absolute;
	top: 15px;
	right: 15px;
	padding: 0;
	margin: 0;
	background: transparent;
	translate: 0px 2px;
	display: flex;
	align-items: flex-start;
	width: 30px;
}

//////////////////////////////////
///////// control styles /////////
//////////////////////////////////

button {
	cursor: pointer;
	background-color: $secondaryColor;
	outline: none;
	border: none;
	font-size: 20px;
	font-family: $font;
	margin-top: 20px;
	padding: 6px;
	border-radius: 5px;
}

input {
	outline: none;
	border: none;
	border-radius: 5px;
	caret-shape: block;
	font-family: $font;
	background-color: $lighterBackground;
	box-shadow: 3px 2px 10px rgba(52, 53, 55, 0.358);

	&::-webkit-input-placeholder {
		font-size: 0.9em;
	}

	&::-moz-placeholder {
		font-size: 0.9em;
	}

	&:-ms-input-placeholder {
		font-size: 0.9em;
	}

	&::-ms-input-placeholder {
		font-size: 0.9em;
	}

	&::placeholder {
		font-size: 0.8em;
	}
}